import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import ButtonText from '../../button-text';
import Col from '../../col';
import Grid from '../../grid';
import Icon from '../../icon';
import Panel from '../../panel';
import PanelContent from '../../panel-content';
import PanelPanel from '../../panel-panel';
import PanelTabs from '../../panel-tabs';
import PanelTabsClose from '../../panel-tabs-close';
import Row from '../../row';
import Tab from '../../tab';
import TabList from '../../tab-list';
import Typography from '../../typography';
import ButtonAction from '../../button-action';

const PanelExample = () => {
  const [state, setState] = useState({
    hide: false,
    open: false,
    selectedTabId: undefined
  });

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Panel composed with state</Typography>
          <Typography>
            Use the approach below to create wrappers that
            satisfy requirements of your product
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <ButtonText
            onClick={() => setState({
              ...state,
              hide: !state.hide
            })}
          >
            {state.hide ? 'Show' : 'Hide'} Panel
          </ButtonText>

          <ButtonText
            onClick={() => setState({
              ...state,
              open: !state.open,
              selectedTabId: state.selectedTabId || 0
            })}
          >
            {state.open ? 'Close' : 'Open'} Panel
          </ButtonText>
          {
            ReactDOM.createPortal(
              <Panel hide={state.hide} open={state.open}>
                <PanelTabs>
                  <TabList orientation="vertical" selectedTab={state.selectedTabId}>
                    <Tab
                      aria-label="Global Settings"
                      onClick={() => setState({ ...state, open: true, selectedTabId: 0 })}
                      tag={<button />}
                    >
                      <Icon name="global" resolution="low" />
                    </Tab>
                    <Tab
                      aria-label="Merchant Settings"
                      onClick={() => setState({ ...state, open: true, selectedTabId: 1 })}
                      tag={<button />}
                    >
                      <Icon name="merchant" resolution="low" />
                    </Tab>
                    <Tab
                      aria-label="Calendar View"
                      onClick={() => setState({ ...state, open: true, selectedTabId: 2 })}
                      tag={<button />}
                    >
                      <Icon name="calendar" resolution="low" />
                    </Tab>
                    <Tab
                      aria-label="Other Settings"
                      onClick={() => setState({ ...state, open: true, selectedTabId: 3 })}
                      tag={<button />}
                    >
                      <Icon name="controls" resolution="low" />
                    </Tab>
                  </TabList>
                </PanelTabs>
                <PanelPanel>
                  <PanelContent show={state.open && state.selectedTabId === 0}>
                    <Typography tag="h2">Panel Title</Typography>
                    <Typography tag="h3" variant="subtitle">Panel Subtitle</Typography>
                  </PanelContent>
                  <PanelContent show={state.open && state.selectedTabId === 1}>
                    <Typography tag="h2">Panel Second Title</Typography>
                    <Typography tag="h3" variant="subtitle">Panel Subtitle</Typography>
                  </PanelContent>
                  <PanelContent show={state.open && state.selectedTabId === 2}>
                    <Typography tag="h2">Panel Third Title</Typography>
                    <Typography tag="h3" variant="subtitle">Panel Subtitle</Typography>
                  </PanelContent>
                  <PanelContent show={state.open && state.selectedTabId === 3}>
                    <Typography tag="h2">Panel Fourth Title</Typography>
                    <Typography tag="h3" variant="subtitle">Panel Subtitle</Typography>
                  </PanelContent>
                </PanelPanel>
                <PanelTabsClose
                  aria-label="Toggle Panel"
                  aria-expanded={state.open}
                  onClick={() => setState({
                    ...state,
                    open: !state.open,
                    selectedTabId: state.selectedTabId || 0
                  })}
                >
                  <ButtonAction>
                    {
                      state.open
                        ? <Icon name="arrow-next" resolution="low" />
                        : <Icon name="arrow-previous" resolution="low" />
                    }
                  </ButtonAction>
                </PanelTabsClose>
              </Panel>, document.getElementById('viewport')
            )
          }
        </Col>
      </Row>
    </Grid>
  );
};

export default PanelExample;
