import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';
import ButtonMenu from '../button-menu';

const CSS_PREFIX = 'vds-panel-tabs-close';

const PanelTabsClose = forwardRef(({ children, className, ...remainingProps }, ref) => (
  <ButtonMenu
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </ButtonMenu>
));

PanelTabsClose.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

PanelTabsClose.displayName = 'PanelTabsClose';

export default PanelTabsClose;
