import React, { forwardRef } from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-panel';
const CSS_STATE = 'vds-state';

const Panel = forwardRef(({
  animated,
  children,
  className,
  hide,
  open,
  ...remainingProps
}, ref) => (
  <div
    aria-expanded={open ? 'true' : 'false'}
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--close`]: !open,
      [`${CSS_STATE}--open`]: Boolean(open),
      [`${CSS_STATE}--animated`]: Boolean(animated),
      [`${CSS_STATE}--hidden`]: Boolean(hide)
    }, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </div>
));

Panel.propTypes = {
  /**
   * Animated
   */
  animated: bool,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Hide
   */
  hide: bool,

  /**
   * Open
   */
  open: bool
};

Panel.displayName = 'Panel';

export default Panel;
