import React from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-panel-content';
const CSS_STATE = 'vds-state';

const PanelContent = ({ children, className, show, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--show`]: Boolean(show)
    }, className)}
    role="tabpanel"
    tabIndex={show ? '0' : '-1'}
    {...remainingProps}
  >
    {children}
  </div>
);

PanelContent.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Show
   */
  show: bool
};

PanelContent.displayName = 'PanelContent';

export default PanelContent;
